import React, {useEffect, useState} from "react";
import {mainTest} from "../api/Api";

const MainComponent = () => {
    const [message,setMessage] = useState("");


    useEffect(()=>{



        mainTest()
            .then(response => {
                // console.log(response);
                console.log("api/main")
                    setMessage(response.message)
            }).catch(error =>{
            console.log(error)
        });



    })





    return (
        <div>
            api : {message}, ,    api  .
            <br/>
            Main 
        </div>
    )
}

export default MainComponent ;