import styled from 'styled-components'
import React, {useState} from 'react'
import {useNavigate} from "react-router";
import Button from "../component/JoinButton";
import {ACCESS_TOKEN, login} from "../api/Api";
import {KAKAO_AUTH_URL} from "../api/KaKaoUrl";
import '../styles/style.css'
import KakaoLogin from "react-kakao-login";

function LoginComponent () {


    // navagate
    const navigate = useNavigate();
    const [userInfo, setUserInfo] = useState({
        membId: "",
        membPw: "",
    })

    // id, pw input handle event
    const handleChange = (e) => {
        setUserInfo({
            ...userInfo,
            [e.target.name]: e.target.value,
        })
    }

    //   
    const handleSubmit = (e) => {

        //    ?
        e.preventDefault();

        // client  login 
        let loginDto = {
            membId: userInfo.membId,
            membPw: userInfo.membPw,
        }


        // login api request - fetch api
        login(loginDto)
            .then(response => {
                // login api request success response  accessToken   session local set .
                sessionStorage.setItem(ACCESS_TOKEN, response.accessToken);
                console.log(response)
                // console.log("accessToken : " + response.accessToken);
                // console.log("tokenType :  " + response.tokenType);
                // console.log("role : " + response.role);

                // let jwt = response.accessToken;
                //
                // let jwtData = jwt.split('.')[1];
                // let decodedJwtJsonData = window.atob(jwtData);
                // let decodedJwtData = JSON.parse(decodedJwtJsonData);
                // let isAdmin = decodedJwtData.roles;
                // console.log('Is admin: ' + isAdmin);
                // console.log("==================")
                // console.log('jwtData: ' + jwtData)
                // console.log('decodedJwtJsonData: ' + decodedJwtJsonData)
                // console.log('decodedJwtData: ' + decodedJwtData)
                console.log(' !');
                navigate("/api/common/mypage");
            }).catch(error => {
            alert('  .');
        });
        // console.log("login  ")
    }

    //
    // const startLogin=()=>{
    //
    // }

    return (
        <div>
            <h3> </h3>
            <form onSubmit={handleSubmit}>
            <Input
                id="id"
                style={{flexGrow: 1}}
                placeholder={"ID   "}
                name="membId"
                value={userInfo.membId}
                onChange={handleChange}
            />
            <Input
                type="password"
                style={{ flexGrow: 1 }}
                placeholder="PASSWORD   "
                name="membPw"
                value={userInfo.membPw}
                onChange={handleChange}
            />
            <Button
                style={{ flexGrow: 1 }}
                type="submit"
                value=""
            />
            </form>

            <a
                // onClick={startLogin}
                href={KAKAO_AUTH_URL}
            >
                <div
                    className="kakao_btn"
                >
                </div>
            </a>

            {/*<KaKaoBtn href={KAKAO_AUTH_URL}>*/}
            {/*    <span> </span>*/}
            {/*</KaKaoBtn>*/}
        </div>
    )
}

const Input = styled.input`
border-radius: 4px;
border: 2px solid #e8e8e8;
padding: 10px;
font-size: 1rem;
margin: 15px;

&:focus {
    border: 2px solid #d32f2f;
    outline: none;
}
::placeholder {
    font-size: 0.8rem;
}

::-webkit-outer-spin-button,
::-webkit-inner-spin-button {
-webkit-appearance: none;
margin: 0;
}
`;

const KaKaoBtn = styled.div`
border-radius: 4px;
border: 2px solid yellow;
background-color:yellow;
padding: 10px;
font-size: 1rem;
margin: 15px;


::-webkit-outer-spin-button,
::-webkit-inner-spin-button {
-webkit-appearance: none;
margin: 0;
}
`;

export default LoginComponent