import React, {useEffect, useState} from "react";
import {batisTest, fetchTest} from "../api/Api";


const TestComponent = () => {

    const [test, setTest] = useState([]);
    const [test2, setTest2] = useState("");
    const [lists, setLists] = useState([]);
    const [selectAll, setSelectAll] = useState([]);



    useEffect(() => {
        fetchTest()
            .then(response => {
                console.log("--jpa api--")
                console.log(response.test2[0].role)
                // console.log("Authority : "+ JSON.stringify(response.test2[0].authority));
                // console.log("-- login Hst --");
                console.log(response.test3);
                setTest(response.test);
                setTest2(response.test2[0].role);
                setLists(response.test3);
            }).catch(error => {
            alert("   . \n   .")
            console.log(error);
        }, []);

        batisTest()
            .then(response => {
                console.log("--mybatis api--")
                console.log(response.selectAll);
                setSelectAll(response.selectAll);
            }).catch(error => {
            console.log("batis error" + error)
        }, []);


    }, []);


    return (
        <div>
              name : {test}
            <br/>
              Authorities : {test2}
            <br/>
              api 
            <br/>
            Test 
            <br/>
            <div style={{border: "1px solid black"}}>
                <h2>  : JPA</h2>
                {
                    lists.map((lists) =>
                        <div key={lists.loginSn}>
                            <span>{lists.loginSn} : {lists.frstRegistDt} ,</span>
                            <span> id : {lists.memberSn.membId} , </span>

                            <span>
                                     :
                                {
                                    lists.memberSn.membCls.codeValue === "ROLE_ADMIN"
                                        ? ""
                                        :
                                        ""

                                }
                                </span>

                        </div>
                    )
                }

            </div>
            <div style={{border:"1px solid black"}}>
                <h2>  : mybatis</h2>
                {
                    selectAll.map((lists)=>
                        <div key={lists.memb_sn}>
                            <span>Sn : {lists.memb_sn} ,Nm : {lists.memb_nm} , Id : {lists.memb_id} ,Email : {lists.email_addr}</span>
                        </div>
                    )
                }
            </div>
        </div>
    )
}

export default TestComponent;